.form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  width: 100%;
}

input {
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: 1px solid var(--color-borde-claro, #ccc);
  border-radius: 0.5rem;
  background-color: var(--color-input-fondo, #f9f9f9);
  transition: border-color 0.2s ease;
}

input:focus {
  outline: none;
  border-color: var(--color-principal, #e91e63);
  background-color: #fff;
}

button {
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-weight: 600;
  background-color: var(--color-principal, #e91e63);
  color: white;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: var(--color-principal-hover, #d81b60);
}

a {
  font-size: 0.9rem;
  text-align: center;
  color: var(--color-principal, #e91e63);
  text-decoration: underline;
  transition: color 0.2s ease;
}

a:hover {
  color: var(--color-principal-hover, #d81b60);
}

.link {
  margin-top: -0.5rem;
}
